/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import { BrowserHistory } from 'history';
import RoundButton from './round-btn';

type hoverPageProps = {
    show: boolean;
    hideHoverPage: Function;
    history: BrowserHistory;
}

export default class HoverPage extends React.Component<hoverPageProps> {
    state = {
        show: this.props.show,
        display: "none",
        opacity: 0,
        scale: 1.1
    }
    render() {
        const hoverPageStyle = css`
            display: ${this.state.display};
            position: fixed;
            z-index: 996;
            height: 100%;
            width: 1170px;
            top: 0;
            left: 50%;
            transform: translateX(-50%) scale(${this.state.scale});
            border-left: 5px solid #000;
            border-right: 5px solid #000;
            opacity: ${this.state.opacity};
            transition: 300ms;
            background: #FFF;
            @media screen and (max-width: 1300px) {
                width: 90%;
            }
            @media screen and (max-width: 800px) {
                width: 100%;
            }
        `
        const shadowStyle = css`
            display: ${this.state.display};
            position: fixed;
            opacity: ${this.state.opacity};
            width: 100%;
            height: 100%;
            top: 0;
            background: rgba(0,0,0,0.1);
        `
        const closeButtonStyle = css`
            z-index: 1;
            position: absolute;
            right: 0;
            top: 0;
            button {
                font-family: "iconfont";
                font-size: 24px;
                color: #000;
            }
        `

        const innerStyle = css`
            height: 100%;
            padding: 0 30px;
            overflow: auto;
            @media screen and (max-width: 800px) {
                padding: 0 20px;
            }
        `

        return(
            <div>
                <div className="hover-page" css={hoverPageStyle}>
                    <div css={closeButtonStyle}><RoundButton onClick={this.close}>&#xe615;</RoundButton></div>
                    <OverlayScrollbarsComponent className="hover-page-inner" css={innerStyle} options={{scrollbars:{autoHide: 'scroll'}}} >
                        <div css={css`padding-top: 40px; padding-bottom: 20px`}>
                            {this.props.children}
                        </div>
                    </OverlayScrollbarsComponent>
                </div>
                <div css={shadowStyle}></div>
            </div>
        )
    }
    static getDerivedStateFromProps(props: hoverPageProps, state: any) {
        const {show} = props;
        if (show !== state.show) {
            return {show};
        }
        return null;
    }
    close = () => {
        this.props.hideHoverPage();
        this.props.history.back();
    }
    back = () => {
        this.props.history.push(window.location.href);
        this.close();
    }
    componentDidUpdate = () => {
        if (this.state.show && this.state.opacity !== 1 && this.state.display !== "block") {
            window.addEventListener('popstate', this.back);
            this.setState({display: "block"}, ()=>{
                setTimeout(()=>{
                    this.setState({opacity: 1, scale: 1})
                })
            })
        } else if (!this.state.show && this.state.opacity !== 0 && this.state.display !== "none") {
            window.removeEventListener('popstate', this.back);
            this.setState({opacity: 0, scale: 1.1}, ()=>{
                setTimeout(()=>{
                    this.setState({display: "none"});
                }, 300)
            })
        }
    }
}